import React, { useEffect, useRef, useState } from 'react';
import useSWR from 'swr';
import { NavLink, useNavigate } from 'react-router-dom';

import { useMediaQuery } from 'react-responsive';
import { RiShutDownLine } from 'react-icons/ri';
import { IconContext } from 'react-icons/lib';
import { CgUserList } from 'react-icons/cg';
import { HiOutlineHome } from 'react-icons/hi';
import { MdOutlineManageAccounts } from 'react-icons/md';

import { Button } from '@styles/Button';
import {
  BottomContainer,
  LogoutBtn,
  NavItem,
  NavList,
  UserBox,
  UserInfo,
  Wrapper,
} from './styles';

const NavigationBar = () => {
  const navigate = useNavigate();

  const { data: userInfo, mutate: upadateUserState } = useSWR('/user/my');

  const [clickLogoutIcon, setClickLogoutIcon] = useState<boolean>(false);
  const userInfoEl = useRef<HTMLDivElement>(null);
  const logoutBoxRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    window.addEventListener('click', userIconBoxHandler, { capture: true });

    return () => {
      window.removeEventListener('click', userIconBoxHandler);
    };
  }, []);

  const userIconBoxHandler = (e: MouseEvent) => {
    if (
      e.target instanceof (HTMLElement || SVGAElement) &&
      !userInfoEl.current?.contains(e.target) &&
      !logoutBoxRef.current?.contains(e.target)
    ) {
      setClickLogoutIcon(false);
    }
  };

  const MobileNav = ({ children }: any) => {
    const isMobile = useMediaQuery({ maxWidth: 1023 });
    return isMobile ? children : null;
  };

  const DeskTopNav = ({ children }: any) => {
    const isDesktop = useMediaQuery({ minWidth: 1024 });
    return isDesktop ? children : null;
  };

  const onClickLogOut = async () => {
    sessionStorage.clear();
    upadateUserState({ logInState: 'LoggingOut' }).then(async () => {
      navigate('/');
    });
  };

  const onClickLogoutBox = (e: React.MouseEvent<HTMLDivElement>) => {
    if (userInfoEl.current?.contains(e.target as HTMLElement)) return;

    setClickLogoutIcon((prev) => !prev);
  };

  return (
    <Wrapper>
      <BottomContainer>
        <MobileNav>
          <NavList>
            <NavItem>
              <NavLink to={'/my_page'} className="nav_item">
                <MdOutlineManageAccounts />
                 
              </NavLink>
            </NavItem>
            <NavItem>
              <h1>
                <NavLink to={'/'}>
                  <HiOutlineHome />
                  ImageUs
                </NavLink>
              </h1>
            </NavItem>
            <NavItem>
              <NavLink to={'/people_management'} className="nav_item">
                <CgUserList />
                
              </NavLink>
            </NavItem>
          </NavList>
          <div
            className="user_icon_d"
            onClick={onClickLogoutBox}
            ref={logoutBoxRef}
          >
            <IconContext.Provider
              value={{
                size: '18px',
                style: { display: 'inline-block', margin: 0 },
              }}
            >
              <RiShutDownLine />
            </IconContext.Provider>
            {clickLogoutIcon && (
              <UserBox ref={userInfoEl}>
                <UserInfo>
                  <div className={'info_words'}>
                    <p>
                      <strong>{userInfo.userInfo?.name ?? 'loading..'}</strong>{' '}
                       !
                    </p>
                    <p>
                      <strong>email:</strong>{' '}
                      {userInfo.userInfo?.email ?? '..'}
                    </p>
                  </div>
                </UserInfo>
                <LogoutBtn>
                  <Button
                    className="error"
                    type="button"
                    onClick={onClickLogOut}
                  >
                    
                  </Button>
                </LogoutBtn>
              </UserBox>
            )}
          </div>
        </MobileNav>
        <DeskTopNav>
          <NavList>
            <NavItem>
              <h1>
                <NavLink to={'/'}>
                  <HiOutlineHome />
                  ImageUs
                </NavLink>
              </h1>
            </NavItem>
            <NavItem>
              <NavLink to={'/my_page'} className="navigate_link">
                 
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink to={'/people_management'} className="navigate_link">
                
              </NavLink>
            </NavItem>
          </NavList>
          <div
            className="user_icon_d"
            onClick={onClickLogoutBox}
            ref={logoutBoxRef}
          >
            <IconContext.Provider
              value={{
                size: '18px',
                style: { display: 'inline-block', margin: 0 },
              }}
            >
              <RiShutDownLine />
            </IconContext.Provider>
            {clickLogoutIcon && (
              <UserBox ref={userInfoEl}>
                <UserInfo>
                  <div className={'info_words'}>
                    <p>
                      <strong>{userInfo.userInfo?.name ?? 'loading..'}</strong>{' '}
                       !
                    </p>
                    <p>
                      <strong>email:</strong>{' '}
                      {userInfo.userInfo?.email ?? '..'}
                    </p>
                  </div>
                </UserInfo>
                <LogoutBtn>
                  <Button
                    className="error"
                    type="button"
                    onClick={onClickLogOut}
                  >
                    
                  </Button>
                </LogoutBtn>
              </UserBox>
            )}
          </div>
        </DeskTopNav>
      </BottomContainer>
    </Wrapper>
  );
};

export default NavigationBar;
